:root {
  --primary: #D97C8A;
  --primary-dark: #C56A79;
  --primary-light: #E8A4AF;
  --secondary: #FAF7F8;
  --accent-gold: #F7D88A;
  --accent-rose: #E8C4C8;
  --success: #4CAF50;
  --danger: #E64A4A;
  --info: #5C9CE5;
  --text-primary: #2A2A2A;
  --text-secondary: #6F6F6F;
  --text-muted: #9A9A9A;
  --border-soft: #EAEAEA;
  --background: #FFFFFF;
  --background-muted: #FDF9FA;
  --spacing-md: 16px;
  --spacing-lg: 24px;
  --spacing-section: clamp(80px, 10vw, 140px);
  --radius-medium: 12px;
  --radius-round: 999px;
  --ease-default: cubic-bezier(0.25, 0.1, 0.25, 1);
  --duration-fast: 150ms;
  --duration-normal: 250ms;
  --max-width: 1240px;
  --foreground: var(--text-primary);
  --card: var(--background);
  --card-foreground: var(--text-primary);
  --popover: var(--background);
  --popover-foreground: var(--text-primary);
  --primary-foreground: white;
  --secondary-foreground: var(--text-primary);
  --muted: var(--background-muted);
  --muted-foreground: var(--text-secondary);
  --accent: var(--accent-rose);
  --accent-foreground: var(--text-primary);
  --destructive: var(--danger);
  --destructive-foreground: white;
  --border: var(--border-soft);
  --input: var(--secondary);
  --ring: var(--primary);
  --radius: var(--radius-medium);
}
.dark {
  --primary: #E8939F;
  --primary-dark: #D97C8A;
  --primary-light: #F0B5BD;
  --secondary: #2A2428;
  --accent-gold: #D4B96A;
  --accent-rose: #8B6B70;
  --text-primary: #F5F0F1;
  --text-secondary: #A89B9D;
  --text-muted: #7A6E70;
  --border-soft: #3D3539;
  --background: #1A1618;
  --background-muted: #231F21;
  --foreground: #F5F0F1;
  --border: #3D3539;
}
@layer base {
  * {
    border-color: var(--border-soft);
  }
  html {
    scroll-behavior: smooth;
  }
  body {
    background-color: var(--background);
    color: var(--text-primary);
    font-family: var(--font-be-vietnam), "Be Vietnam Pro", system-ui, sans-serif;
    font-size: 17px;
    line-height: 1.65;
  }
  h1, h2, h3, h4, h5, h6 {
    font-family: var(--font-cormorant), "Cormorant Garamond", Georgia, serif;
    font-weight: 600;
    line-height: 1.25;
    color: var(--text-primary);
  }
  h1 {
    font-size: clamp(48px, 5vw, 64px);
    letter-spacing: -0.02em;
  }
  h2 {
    font-size: clamp(36px, 4vw, 44px);
    letter-spacing: -0.01em;
  }
  h3 {
    font-size: clamp(26px, 3vw, 30px);
    font-weight: 500;
  }
  h4 {
    font-size: clamp(20px, 2.5vw, 24px);
    font-weight: 500;
  }
  p {
    font-size: 17px;
    line-height: 1.65;
    color: var(--text-secondary);
  }
  ::selection {
    background: var(--primary);
    color: white;
  }
  :focus-visible {
    outline: 2px solid var(--primary);
    outline-offset: 2px;
  }
}
@layer utilities {
  .container-premium {
    max-width: var(--max-width);
    margin-left: auto;
    margin-right: auto;
    padding-left: var(--spacing-md);
    padding-right: var(--spacing-md);
  }
  @media (min-width: 1024px) {
    .container-premium {
      padding-left: var(--spacing-lg);
      padding-right: var(--spacing-lg);
    }
  }
  .section-padding {
    padding-top: var(--spacing-section);
    padding-bottom: var(--spacing-section);
  }
  .transition-premium {
    transition: all var(--duration-normal) var(--ease-default);
  }
  .transition-fast {
    transition: all var(--duration-fast) var(--ease-default);
  }
  .text-gradient-primary {
    background: linear-gradient(135deg, var(--primary) 0%, var(--primary-dark) 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
  }
  .hover-lift {
    transition: transform var(--duration-normal) var(--ease-default);
  }
  .hover-lift:hover {
    transform: translateY(-4px);
  }
  .hover-scale {
    transition: transform var(--duration-normal) var(--ease-default);
  }
  .hover-scale:hover {
    transform: scale(1.03);
  }
  .scrollbar-hide {
    -ms-overflow-style: none;
    scrollbar-width: none;
  }
  .scrollbar-hide::-webkit-scrollbar {
    display: none;
  }
}
@keyframes fade-up {
  from {
    opacity: 0;
    transform: translateY(24px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes scale-in {
  from {
    opacity: 0;
    transform: scale(0.95);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}
@keyframes slide-up {
  from {
    transform: translateY(100%);
  }
  to {
    transform: translateY(0);
  }
}
@keyframes float {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-12px);
  }
}
@keyframes shimmer {
  from {
    background-position: -200% 0;
  }
  to {
    background-position: 200% 0;
  }
}
.animate-fade-up {
  animation: fade-up 0.6s var(--ease-default) forwards;
}
.animate-fade-in {
  animation: fade-in 0.4s var(--ease-default) forwards;
}
.animate-scale-in {
  animation: scale-in 0.3s var(--ease-default) forwards;
}
.animate-slide-up {
  animation: slide-up 0.4s var(--ease-default) forwards;
}
.animate-float {
  animation: float 3s ease-in-out infinite;
}
.animate-shimmer {
  background: linear-gradient(90deg, var(--background-muted) 25%, var(--background) 50%, var(--background-muted) 75%);
  background-size: 200% 100%;
  animation: shimmer 1.5s infinite;
}
.delay-100 {
  animation-delay: 100ms;
}
.delay-200 {
  animation-delay: 200ms;
}
.delay-300 {
  animation-delay: 300ms;
}
@media (prefers-reduced-motion: reduce) {
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
  }
